import React,{useState} from 'react';

const Items = props=>{

    const list = props.list

    // edit options
    const [visible, toggleVisible] = useState(true)

    const done = visible? "Edit": "Done"





    
    return <div className = "singleList"
    style={{display: props.visible? "block": "none"}}
    // style = {{display: props.visible? "inline": "none"}}
    >
        {list.map((item, index)=>{

            return <div key = {index}>
                <p
                    style={{display: visible? "block": "none"}}
                    key = {item}
                    onClick = {event=>{
                        props.addToPlan(item, props.group, list)
                    }}
                    >{item}
                </p>

                <p
                    style={{display: visible? "none":"block"}}
                    onClick = {event=>{
                        event.preventDefault()
                        props.removeFromFoods(item, props.group)

                    }}
                >DELETE {item}</p>
                
            </div>
        })}
        <div className = "editFoodGroup">

            <input 
                style={{display: visible? "none":"inline"}}
                className = "edit"
                placeholder = "Add an Item"
                onKeyDown = {event=>{
                    if (event.keyCode === 13){
                        let item = event.target.value
                        props.addToFoods(item, props.group)
                        event.target.value = ""

                    }                   
                }}
            
            />

            

            <button
                className = "done"
                onClick = {event=>{
                    event.preventDefault()
                    toggleVisible(prev=>!prev)

                }}
            >{done}</button>

        </div>

    </div>
}

export default Items
