import React, { useState } from "react"
import Day from "./Day"


const MealPlan = props=>{
    const date = props.Data.startDate

    return <div>
        <h1>Meal Plan</h1>

        <label>Select a starting date</label>
        <input 
        id = "date"
        type = "date"
        onChange = {event=>{
            let newDate = event.target.valueAsDate
            props.changeStartDate(newDate)
        }}
        />

        <Day 
        mealPlan = {props.Data.mealPlan}
        updateItem = {props.updateItem}
        date = {date}
        />
    </div>
}

export default MealPlan
